import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import './App.css';

import Home from './Pages/Home/index';
import Exercise1 from './Pages/Exercise1/index';
import Exercise2 from './Pages/Exercise2/index';

function App() {
  return (
      <Router>
        <main>
          <Switch>
            <Route path="a11y-exercises/ex1">
              <Exercise1 />
            </Route>
            <Route path="a11y-exercises/ex2">
              <Exercise2 />
            </Route>
            <Route path="/a11y-exercises">
              <Home />
            </Route>
          </Switch>
        </main>
      </Router>
  );
}

export default App;
